                            <div class="sub_menu">市场</div>
                        </template>
                        <template #content>
                            <div class="p10" style="background: #17171a;">
                                <div class="subMenus   ">
                                    <div v-for="(v,o) in areaList" :key="o" class="secondMenu" :class="{'active':active==o}" @click="active=o;getExchange();">{{v.text}}</div>
                                </div>
                            </div>
                        </template>
                    </Poptip> -->
