                        <td>有效期</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(v, o) in managerList" :key="o" :class="{'active':tableActive2==o}" @click="tableActive2=o">
                        <td class="">{{ o+1 }}</td>
                        <td class="tleft">{{ v.commitDate }}</td>
                        <td class="">{{v.varietyId}}</td><!--福建省碳排放量-->
                        <td>
                            <span v-if="v.direction=='买'" class="red">买入</span>
                            <span v-else-if="v.direction=='卖'" class="green">卖出</span>
                        </td><!--B buy  S sell-->
                        <td>{{ v.vol | numFormat(2) }}</td>
                        <td>{{ v.price | numFormat(2) }}</td>
                        <td>{{ v.contractName }}</td>
                        <td class="">{{ v.phone }}</td>
                        <td class="">{{v.expireDate}}</td><!--1个月-->
                    </tr>
                    <tr v-if="managerList.length==0">
                        <td colspan="9" class="center cloc">暂无数据</td>
                    </tr>
                </tbody>
            </table>
