                            <td class="green">15000</td>
                            <td class="">877621.15</td>
                            <td class="">1%</td>
                        </tr> -->
                </tbody>
            </table>
        </div>
    </div>
    
</div>
<div style="margin-left:10px;flex:1;">
    <div class="block">
        <!-- 国外碳价行情 -->
        <div class=" p15" style="background-color: rgb(30, 32, 38);border-radius: 8px;">
            <div class="f18 clof p5_0 ">国外碳价行情</div>
            <div class="mt10">
                <div id="chart-panel" style="height: 400px;width: 100%"></div>
            </div>
        </div>
    </div>
</div>
</div>
</template>

<script>

export default {
    name: "",
    props: {},
    components: {},
    data() {
        return {
            tableActive:-1,
            tableActive2:-1,
            tableActive3:-1,
            active:0,
            // areaList:this.$store.state.app.areaList,//区域列表
            areaList:[{
                type:'china',
                text:'国内',
            },{
                type:'eu',
                text:'国外',
            },{
                type:'other',
                text:'其他',
            }],//区域列表
            exchangeList:[],
            ccerList:[],
            managerList:[],
        };
    },
    mounted() {
        this.getExchange();
        this.getCcer();
        this.getManager();
        this.getList();
    },
    methods: {
        async getExchange(){
            let d={
                area: this.areaList[this.active].type,
                current: 1,
                size: 5,
            };
            let res = await this.$api.market_page(d);
            let list = res.list || [];
            this.exchangeList=list;
        },
        async getCcer(){
            let d={
                current: 1,
                size: 10,
            };
            let res = await this.$api.ccer_page(d);
            let list = res.list || [];
            this.ccerList=list;
        },
        async getManager(){
            let d={
                current: 1,
                size: 5,
            };
            let res = await this.$api.market_block_page(d);
            let list = res.list || [];
            this.managerList=list;
        },
        async getList(){
            let d={
                // area: this.areaList[this.active].type,
                // current: 1,
                // size: 5,
            };
            let res = await this.$api.internation(d);
            let list = res || [];
            let typeList=[],dataList=[];
            let timeList=[];
            list.forEach((v,o)=>{
                // itemStyle: {
                //         //     normal: {
                //         //         color: "#ff0000",
                //         //         lineStyle: {
                //         //         color: "#ff0000"
                //         //         }
                //         //     }
                //         // }
                typeList.push(v.typeName);
                // typeList.push(v.typeName.substr(0,2));
                let data=[];
                v.internationalViewVOS.forEach((item,index)=>{
                    data.push(item.price);
                    if(o==0){   //timeList取第一次循环
                        // timeList.push(item.date);
                        timeList.push(this.$dayjs(item.date).format('YYYY-MM-DD'));
                    }
                });
                dataList.push({
                    name: v.typeName,
                    // name: v.typeName.substr(0,2),
                    type: "line",
                    stack: "Total",
                    // data: [220, 182, 191, 234, 290, 330, 310],
                    data: data,
                },);
            });
            console.log('结果：',typeList,timeList,dataList)
            // this.list=list;
            this.initCharts(typeList,timeList,dataList)
        },
        //图表
        //typeList：类别列表。timeList：时间列表。dataList：数据列表
        initCharts(typeList,timeList,dataList) {
            // console.log('结果：',typeList,dataList)
            var dom = document.getElementById("chart-panel");
            var myChart = echarts.init(dom);
            var option;
            var app = {};
            option = {
                title: {
                    // text: "走势",
                },
                tooltip: {
                    trigger: "axis",
                },
                legend: {
                    data: typeList,
                    // data:[
                    //     "美国",
                    //     "英国",
                    //     "日本",
                    //     "韩国",
                    //     "俄国",
                    //     "中国",
                    //     "法国",
                    // ],
                    
                    textStyle:{
                        color: '#ccc',
                        fontSize: 14
                    }
                },
                
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true,
                },
                toolbox: {
                    // feature: {
                    //     saveAsImage: {},
                    // },
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    // data: ["2016", "2017", "2018", "2019", "2020", "2021", "2022"],
                    data: timeList,
                    axisLabel: {//y轴文字的配置
                            textStyle: {
                                color: "rgba(138, 144, 163, 1)",//Y轴内容文字颜色
                            },
                    },
                    axisLine: {//y轴线的配置
                            show: true,//是否展示
                            lineStyle: {
                                color: "#315070",//y轴线的颜色（若只设置了y轴线的颜色，未设置y轴文字的颜色，则y轴文字会默认跟设置的y轴线颜色一致）
                                width: 1,//y轴线的宽度
                                type: "solid"//y轴线为实线
                            },
                    }
                },
                yAxis: {
                    type: "value",
                    axisLabel: {//y轴文字的配置
                            textStyle: {
                                color: "rgba(138, 144, 163, 1)",//Y轴内容文字颜色
                            },
                    },
                    //设置网格线颜色
                    splitLine: {
                        show: true,
                        lineStyle:{
                            color: ['#31507055'],
                            width: 1,
                            type: 'sollid'
                        }
                    }
                    // axisLine: {//y轴线的配置
                    //         show: true,//是否展示
                    //         lineStyle: {
                    //             color: "#ECEDF0",//y轴线的颜色（若只设置了y轴线的颜色，未设置y轴文字的颜色，则y轴文字会默认跟设置的y轴线颜色一致）
                    //             width: 1,//y轴线的宽度
                    //             type: "solid"//y轴线为实线
                    //         },
                    // }
                },
                series: dataList,
                // series:[
                //     {
                //         name: "美国",
                //         type: "line",
                //         stack: "Total",
                //         data: [120, 132, 101, 134, 90, 230, 210],
                //         // itemStyle: {
                //         //     normal: {
                //         //         color: "#ff0000",
                //         //         lineStyle: {
                //         //         color: "#ff0000"
                //         //         }
                //         //     }
                //         // }
                //     },
                //     {
                //         name: "英国",
                //         type: "line",
                //         stack: "Total",
                //         data: [220, 182, 191, 234, 290, 330, 310],
                //     },
                //     {
                //         name: "日本",
                //         type: "line",
                //         stack: "Total",
                //         data: [150, 232, 201, 154, 190, 330, 410],
                //     },
                //     {
                //         name: "韩国",
                //         type: "line",
                //         stack: "Total",
                //         data: [320, 332, 301, 334, 390, 330, 320],
                //     },
                //     {
                //         name: "俄国",
                //         type: "line",
                //         stack: "Total",
                //         data: [820, 932, 901, 934, 1290, 1330, 1320],
                //     },
                //     {
                //         name: "中国",
                //         type: "line",
                //         stack: "Total",
                //         data: [820, 932, 901, 934, 1290, 1330, 1320],
                //     },
                //     {
                //         name: "法国",
                //         type: "line",
                //         stack: "Total",
                //         data: [820, 932, 901, 934, 1290, 1330, 1320],
                //     },
                // ],
            };
            if (option && typeof option === "object") {
                myChart.setOption(option);
            }
        }
    },
};
</script>

<style scoped lang='less'>
.block {
    // width: calc((100% - 40px) / 2);
    margin-bottom: 10px;
}
.toggleBtn{
    cursor: pointer;
    display: inline-block;
    padding:2px 8px;
    font-size: 12px;
    font-weight: normal;
    color: #3861fb;
    background: #2d2d2d;
    border-radius: 15px;
}
//气泡框
:deep(.ivu-poptip-inner){
    background: #2b2f38;
}
:deep(.ivu-poptip-title){
    background: #2b2f38;
}
:deep(.ivu-poptip-title:after){
    content:'';
    display: none;
}
:deep(.ivu-poptip-popper[x-placement^=bottom] .ivu-poptip-arrow:after){
    border-bottom-color: #2b2f38;
}
:deep(.ivu-poptip-popper[x-placement^=bottom] .ivu-poptip-arrow){
    border-bottom-color: rgb(43 47 56);
}


.menus{
    display: flex;
    // position: fixed;
    // top:48px;
    // left:0;
    // background: #17171a;
    width:100%;
    
    padding:5px;
}
.menu{
    cursor: pointer;
