    border-radius: 8px;
    // height:40px;
    // line-height:40px;
}
.menu:hover,
.menu.active{
    white-space: nowrap;
    margin-right: 4px;
    // background-color: rgba(56, 97, 251, 0.1);
    // color: rgb(97, 136, 255);
    background-color: rgba(56, 97, 251, 0.1);
    color: #3861fb;
}
</style>